"use client";
import { useDispatch, useSelector } from "react-redux";
import { toggleSidebar } from "@/store/slices/uiSlice";
import { Menu, Bell, Search } from "lucide-react";

export default function Header() {
  const dispatch = useDispatch();
  const { sidebarOpen } = useSelector((s) => s.ui);

  // Agar sidebar open hai to header mat dikhana
  if (sidebarOpen) return null;

  // Aaj ki date format me
  const today = new Date();
  const options = { day: "2-digit", month: "short", year: "numeric" };
  const formattedDate = today.toLocaleDateString("en-US", options); // ex: May 19, 2025

  return (
    <header className="bg-gray-100 border-b border-gray-300 sticky top-0 z-40">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-3 flex flex-col sm:flex-row items-center justify-between gap-3">
        
        {/* Left section: Hamburger (mobile) + User Info */}
        <div className="flex items-center gap-3 w-full sm:w-auto">
          {/* Mobile Hamburger (ek hi rakha) */}
          <button
            onClick={() => dispatch(toggleSidebar())}
            className="lg:hidden p-2 rounded-md hover:bg-gray-200"
          >
            <Menu size={22} className="text-gray-700" />
          </button>

          {/* User Name + Date */}
          <div className="flex flex-col">
            <span className="text-base sm:text-lg font-semibold text-gray-800">
              Hello, Sarah William{" "}
              <span className="mx-1 text-sm text-gray-400">&gt;&gt;</span>
              <span className="text-sm font-normal text-gray-500">{formattedDate}</span>
            </span>
          </div>
        </div>

        {/* Right section */}
        <div className="flex items-center gap-4 w-full sm:w-auto">
          {/* Notification */}
          <button className="relative p-2 rounded-full hover:bg-gray-200">
            <Bell size={20} className="text-gray-700" />
            <span className="absolute top-1.5 right-1.5 w-2 h-2 bg-red-500 rounded-full"></span>
          </button>

          {/* Search Bar */}
          <div className="relative flex-1 sm:flex-none w-full sm:w-64 md:w-80">
            <input
              type="text"
              placeholder="Search..."
              className="w-full pr-9 pl-3 py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-teal-500"
            />
            <Search
              size={18}
              className="absolute right-2.5 top-1/2 -translate-y-1/2 text-gray-500"
            />
          </div>
        </div>
      </div>
    </header>
  );
}
